@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --base-light : #EBF5DF;
  --base-dark : #2A2B2A;
  --links : #0c264d;
} 

html {
  scroll-behavior: smooth;
  overflow-x: none;
}

a {
  color: #0c264d;
  color: var(--links);
  text-decoration: underline;
}

body {
  background-image: url(/loginAuth-frontend-build/static/media/bg2.d956610b.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  padding: 0;
  margin: 0;
  font-family: 'Nunito Sans', sans-serif;
}

.main-form {
  width: 400px;
  color: #2A2B2A;
  color: var(--base-dark);
  border-radius: 10px;
  margin: auto;
  padding: 25px;
  margin-top: 20vh;
  margin-bottom: 20vh;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  box-shadow: -2px 4px 101px -4px rgba(0,0,0,0.56);
  border: 1px solid #2A2B2A;
  border: 1px solid var(--base-dark);
}


.main-form-register {
  width: 400px;
  color: #2A2B2A;
  color: var(--base-dark);
  border-radius: 10px;
  margin: auto;
  padding: 25px;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  margin-top: 12vh;
  margin-bottom: 20vh;
  box-shadow: -2px 4px 101px -4px rgba(0,0,0,0.56);
  border: 1px solid #2A2B2A;
  border: 1px solid var(--base-dark);
}

.input-bar {
  margin: 10px 0px 10px 0px;
  width: 98%;
  height: 35px;
  border-radius: 10px;
  border: none;
  outline: 1px solid #2A2B2A;
  outline: 1px solid var(--base-dark);
  padding: 5px 5px 5px 10px;
  font-size: 16px;
  font-family: 'Nunito Sans', sans-serif;
  background-color: transparent;
}

.btn-login {
  outline: none;
  border: none;
  background-color: #2A2B2A;
  background-color: var(--base-dark);
  color: #EBF5DF;
  color: var(--base-light);
  text-align: center;
  width: 40%;
  margin: 10px auto;
  display: block;
  border-radius: 10px;
  font-size: 16px;
  padding: 10px;
  cursor: pointer;
  transition: 0.4s;
}

.btn-register {
  outline: none;
  border: none;
  background-color: #2A2B2A;
  background-color: var(--base-dark);
  color: #EBF5DF;
  color: var(--base-light);
  text-align: center;
  width: 40%;
  margin: 17px auto;
  display: block;
  border-radius: 10px;
  font-size: 16px;
  padding: 10px;
  cursor: pointer;
  transition: 0.4s;
}

.btn-login:hover {
  outline: 1px solid #2A2B2A;
  outline: 1px solid var(--base-dark);
  background-color: transparent;
  transition: 0.4s;
  color: #2A2B2A;
  color: var(--base-dark);
}

.btn-register:hover {
  outline: 1px solid #2A2B2A;
  outline: 1px solid var(--base-dark);
  background-color: transparent;
  transition: 0.4s;
  color: #2A2B2A;
  color: var(--base-dark);
}

.register-prompt {
  margin-bottom: -10px;
  font-size: 12px;
  opacity: 0.8;
}

.prompt {
  font-size: 12px;
  opacity: 0.8;
}

.ehe {
  width: 280px;
  height: 160px;
  border: 2px solid #2A2B2A;
  border: 2px solid var(--base-dark);
}



@media screen and (max-width: 768px) {
  body, html {
      background-repeat: no-repeat;
      scroll-behavior: smooth;
      scroll-snap-type: none;
      margin: 0px;
      padding: 0px;
      overflow-x: hidden;
  }

  .main-form {
    width: 330px;
    color: #2A2B2A;
    color: var(--base-dark);
    border-radius: 10px;
    margin: auto;
    margin-top: 10vh;
    margin-bottom: 10vh;
    padding: 25px;
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    box-shadow: -2px 4px 101px -4px rgba(0,0,0,0.56);
    border: 1px solid #2A2B2A;
    border: 1px solid var(--base-dark);
  }
  
  .main-form-register {
    width: 330px;
    color: #2A2B2A;
    color: var(--base-dark);
    border-radius: 10px;
    margin: auto;
    padding: 25px;
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    margin-top: 10vh;
    margin-bottom: 10vh;
    box-shadow: -2px 4px 101px -4px rgba(0,0,0,0.56);
    border: 1px solid #2A2B2A;
    border: 1px solid var(--base-dark);
  }
}
