  overflow-x: none;
}

a {
  color: #0c264d;
  color: var(--links);
  text-decoration: underline;
}

body {
  background-image: url(/loginAuth-frontend-build/static/media/bg2.d956610b.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  padding: 0;
  margin: 0;
  font-family: 'Nunito Sans', sans-serif;
}

nav {
    background-color: transparent;
    border-bottom: 1px solid #2A2B2A;
    border-bottom: 1px solid var(--base-dark);
}

.button-login {
    background-color: #2A2B2A;
    background-color: var(--base-dark);
    color: #EBF5DF;
    color: var(--base-light);
    padding: 10px 20px 10px 20px;
    width: 90px;
    border-radius: 10px;
    transition: 0.4s;
    border: 1px solid #2A2B2A;
    border: 1px solid var(--base-dark);
}

.button-login:hover {
    background-color: transparent;
    color: #2A2B2A;
    color: var(--base-dark);
    transition: 0.4s;
    border: 1px solid #2A2B2A;
    border: 1px solid var(--base-dark);
}

.landing-wrapper {
    display: flex;
    flex-direction: row;
    grid-gap : 0;
    gap : 0;
    height: 90vh;
}

.left-side {
    color: #2A2B2A;
    color: var(--base-dark);
    margin: 0 auto;
    padding: 70px;
    width: 60%;
}

.right-side {
    color: #2A2B2A;
    color: var(--base-dark);
    margin: 0 auto;
    padding: 100px;
    width: 40%;
    text-align: center;
}

.greeting {
    margin-top: 16vh;
    font-size: 5em;
}

.learn-more {
    background-color: #2A2B2A;
    background-color: var(--base-dark);
    color: #EBF5DF;
    color: var(--base-light);
    padding: 10px 20px 10px 20px;
    width: 150px;
    border-radius: 10px;
    transition: 0.4s;
    border: 1px solid #2A2B2A;
    border: 1px solid var(--base-dark);
    margin: 0 auto;
}

.learn-more:hover {
    background-color: transparent;
    color: #2A2B2A;
    color: var(--base-dark);
    transition: 0.4s;
    border: 1px solid #2A2B2A;
    border: 1px solid var(--base-dark);
}

.right-side-content {
    margin-top: 30vh
}

.about {
    text-align: center;
    height: 100vh;
    padding: 100px;
}

.about-wrapper {
    display: flex;
    flex-direction: row;
    grid-gap: 10px;
    gap: 10px;
}

.about-left-side {
    color: #2A2B2A;
    color: var(--base-dark);
    margin: 0 auto;
    padding: 100px;
